<template>
<div class="section">
  <div class="serch">
    <h4></h4>
    <el-input class="serch-input" v-model="adminerName" placeholder="">
      <i slot="prefix" class="el-input__icon el-icon-search"></i>
    </el-input>
    <el-button class="serch-btn" @click="SerchAdminer" :disabled="disable" type="primary"></el-button>
  </div>
  <div class="alladminers">
    <h4></h4>
    <el-table
    :data="adminers">
    <el-table-column
      min-width="20%"
      prop="time"
      label="">
    </el-table-column>
    <el-table-column
      min-width="20%"
      prop="name"
      label="">
    </el-table-column>
    <el-table-column
      min-width="20%"
      prop="role"
      label="">
    </el-table-column>
    <el-table-column
      min-width="20%"
      prop="avatar"
      label="">
      <template slot-scope="scope">
        <img class="table-avatar" :src="scope.row.avatar" alt="">
      </template>
    </el-table-column>
    <el-table-column
      min-width="20%"
      label="">
       <template slot-scope="scope">
        <el-button
          type="danger"
          @click="EditAdminer(scope.$index, scope.row)"></el-button>
      </template>
    </el-table-column>
  </el-table>
  <!--  -->
  <el-dialog title="" :visible.sync="dialogFormVisible">
    <el-form ref="editadminer" :rules="addadminerRules" :model="adminer">
      <el-form-item prop="name">
        <el-input v-model="adminer.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="role">
        <el-select v-model="adminer.role" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="exitEdit"> </el-button>
      <el-button type="primary" @click="submitEdit"> </el-button>
    </div>
  </el-dialog>
  </div>
</div>
  
</template>
<script>
import {GetAdminer, EditAdminer} from '../../../api/adminApi'
export default {
  // ..
  data () {
    return {
      adminers: [],
      dialogFormVisible: false,
      adminer: '',
      adminerName: '',
      addadminerRules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 2,
            max: 5,
            message: ' 2  5 ',
            trigger: 'blur'
          }
        ],
        role: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  computed: {
    disable () {
      if (this.adminerName.length !== 0) {
        return false
      } else {
        return true
      }
    }
  },
  methods: {
    // 
    SerchAdminer () {
      let SerchParams = {
        name: this.adminerName
      }
      GetAdminer(SerchParams).then(res => {
        // console.log(res)
        if (res.data.length === 0) {
          this.$notify({
            type: 'warning',
            title: '',
            message: '',
            offset: 200
          })
        } else {
          this.adminers = res.data
        }
        this.adminerName = ''
      })
    },
    // 
    EditAdminer (row, rowIndex) {
      // console.log(row, rowIndex)
      this.dialogFormVisible = true
      this.adminer = rowIndex
    },
    // 
    exitEdit () {
      this.dialogFormVisible = false
    },
    // 
    submitEdit () {
      this.$refs.editadminer.validate(valid => {
        if (valid) {
          const updatedParams = {
            id: this.adminer._id,
            name: this.adminer.name,
            role: this.adminer.role
          }
          EditAdminer(updatedParams).then(res => {
            this.$notify({
              title: '',
              message: res.data.name,
              type: 'success',
              offset: 100
            })
            this.dialogFormVisible = false
          })
        } else {
          this.$message({
            type: 'danger',
            message: ''
          })
        }
      })
    }
  },
  mounted () {
    // 
    const adminparams = {
      type: 'all'
    }
    GetAdminer(adminparams).then(res => {
      // console.log(res)
      this.adminers = res.data.adminers
    })
  }
}
</script>
<style lang="less" scoped>
@import '../../../common/less/index.less';
.serch {
  .learncontent;
  .serch-input {
    width: 70%;
  }
  .serch-btn {
    width: 29%;
  }
}
.alladminers {
  .learncontent;
  .el-table {
    .warning-row {
      background: rgba(196, 144, 84, 0.6)
    }
    .success-row {
      background: rgba(136, 175, 91, 0.6)
    }
    .table-avatar {
      width: 50px;
      border-radius: 25px; 
    }
  }
  .el-dialog {
    .el-select {
      width: 100%;
    }
  }
  
}
</style>





