<template>
  <div class="section">
    <!--  -->
    <div class="serchprod">
      <h4></h4>
      <!--  -->
      <el-input class="serch-input" v-model="prodName" placeholder="">
        <i slot="prefix" class="el-input__icon el-icon-search"></i>
      </el-input>
      <el-button class="serch-btn" @click="SerchProd" :disabled="disable" type="primary"></el-button>
    </div>
    <!--  -->
    <div class="allprod">
      <h4></h4>
      <el-table
        :data="prods"
        style="width: 100%">
        <el-table-column
          prop="name"
          label=""
          min-width="40%">
        </el-table-column>
        <el-table-column
          prop="price"
          label=""
          min-width="10%">
        </el-table-column>
        <el-table-column
          prop="image"
          label=""
          min-width="20%">
          <template slot-scope="scope">
            <img class="table-image" :src="scope.row.image" alt="">
          </template>
        </el-table-column>
        <el-table-column
          prop="typename"
          label=""
          min-width="10%"
          :filters="productsFilter"
          :filter-method="filterType"
          filter-placement="bottom-end">
          <template slot-scope="scope">
            <el-tag
              close-transition>{{scope.row.typename}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column
          min-width="20%"
          label="">
          <template slot-scope="scope">
            <el-button
              type="danger"
              @click="EditProd(scope.$index, scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" width="90%">
      <el-form ref="editprod" :rules="prodrules" :model="editprod">
        <el-form-item label="" prop="name">
          <el-input v-model="editprod.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model.number="editprod.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="image">
          <!-- <el-upload
            class="prod-image"
            action="/learn/upload"
            :show-file-list="false"
            :on-success="handleSuccess"
            :before-upload="beforeUpload">
            <img v-if="imageUrl" :src="imageUrl" class="cur-image">
            <i v-else class="el-icon-plus prod-uploader-icon"></i>
          </el-upload> -->
          <qiniu-upload :key="imageUrl" :url="imageUrl" width="200" height="200" fontSize="40" ref="qnupload"></qiniu-upload>
        </el-form-item>
        <el-form-item label="" prop="typename">
          <el-select v-model="editprod.typename" placeholder="">
            <template v-for="item in products">
              <el-option :label="item.name" :value="item.name" :key="item.type"></el-option>
            </template>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input v-model="editprod.type" disabled></el-input>
        </el-form-item>
        
        <el-form-item label="">
          <el-switch v-model="editprod.selling"></el-switch>
        </el-form-item>

        <el-form-item label="" prop="selltime">
          <el-input v-model="editprod.selltime" placeholder=""></el-input>
        </el-form-item>
        
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="editprod.desc" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="info">
          <!-- <mavon-editor  ref="md" @imgAdd="$imgAdd" @imgDel="$imgDel" v-model="editprod.info"></mavon-editor> -->
          <md-upload :key="editprod.info" :info="editprod.info" ref="mdedit"></md-upload>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="exitEdit"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </div>
    </el-dialog>
  </div>
  
</template>
<script>
import {GetProducts, GetProds, EditProd} from '../../../api/adminApi'
// import {UploadFile} from '../../../api/api'
export default {
  // ..
  data () {
    return {
      prodName: '',
      productsFilter: [],
      prods: [],
      products: [],
      dialogFormVisible: false,
      imageUrl: '',
      editprod: {
        name: '',
        price: '',
        type: '',
        typename: '',
        selling: '',
        desc: '',
        info: ''
      },
      prodrules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 3,
            max: 15,
            message: ' 3  15 ',
            trigger: 'blur'
          }
        ],
        price: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            type: 'number',
            message: '',
            trigger: 'blur'
          }
        ],
        type: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ],
        desc: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        selltime: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  computed: {
    disable () {
      if (this.prodName.length !== 0) {
        return false
      } else {
        return true
      }
    }
  },
  watch: {
    'editprod.typename': function (val) {
      // console.log(val)
      // console.log(this.products)
      const CurProduct = this.products.filter(p => {
        return p.name === this.editprod.typename
      })
      // console.log(CurProduct)
      this.editprod.type = CurProduct[0].type
      // console.log(this.addprod.type)
    },
    deep: true
  },
  methods: {
    // 
    SerchProd () {
      let serchpar = {
        name: this.prodName
      }
      GetProds(serchpar).then(res => {
        // console.log(res)
        if (res.data.prods.length > 0) {
          this.$notify({
            title: '',
            type: 'success',
            message: '' + res.data.prods.length + '',
            offset: 200
          })
          this.prods = res.data.prods
          this.prodName = ''
        } else {
          this.$notify({
            title: '',
            type: 'error',
            message: ',',
            offset: 200
          })
          this.prodName = ''
        }
      })
    },
    // 
    getproducts () {
      let productsPar = {
        type: 'all'
      }
      GetProducts(productsPar).then(res => {
        // console.log(res)
        const Oproducts = res.data.products
        this.products = res.data.products
        for (let i = 0; i < Oproducts.length; i++) {
          this.productsFilter.push({
            text: Oproducts[i].name,
            value: Oproducts[i].name
          })
        }
        // console.log(this.productsFilter)
      })
    },
    // 
    getallprods () {
      let allprospar = {
        type: 'all'
      }
      GetProds(allprospar).then(res => {
        // console.log(res)
        this.prods = res.data.prods
      })
    },
    // 
    filterType (value, row) {
      // console.log('value', value)
      // console.log('row', row)
      return row.typename === value
    },
    // 
    EditProd (row, rowIndex) {
      this.editprod = rowIndex
      this.imageUrl = rowIndex.image
      this.dialogFormVisible = true
      // console.log(this.imageUrl)
    },
    // 
    exitEdit () {
      this.dialogFormVisible = false
    },
    // 
    submitEdit () {
      this.$refs.editprod.validate(valid => {
        if (valid) {
          const updatedParams = {
            id: this.editprod._id,
            name: this.editprod.name,
            price: this.editprod.price,
            image: this.$refs.qnupload.imageUrl,
            type: this.editprod.type,
            typename: this.editprod.typename,
            selling: this.editprod.selling,
            desc: this.editprod.desc,
            info: this.$refs.mdedit.mdinfo,
            selltime: this.editprod.selltime
          }
          // console.log(updatedParams)
          EditProd(updatedParams).then(res => {
            this.$notify({
              title: '',
              message: res.data.name,
              type: 'success',
              offset: 100
            })
            this.dialogFormVisible = false
            this.getallprods()
          })
        } else {
          this.$message({
            type: 'danger',
            message: ''
          })
        }
      })
    }
  },
  mounted () {
    // ..
    this.getproducts()
    this.getallprods()
  }
}
</script>
<style lang="less" scoped>
@import '../../../common/less/index.less';
.section {
  .serchprod {
    .learncontent;
    .serch-input {
      width: 70%;
    }
    .serch-btn {
      width: 29%;
    }
  }
  .allprod {
    .learncontent;
    .table-image {
      width: 60px;
      height: 60px;
      border-radius: 30px;
    }
  }
  .el-form {
    text-align: left;
    .el-select {
      width: 100%;
    }
    .el-switch {
      margin: 10px 0 0 0;
    }
    .prod-image {
      width: 200px;
      height: 200px;
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
      .cur-image {
        width: 100%;
      }
      .prod-uploader-icon {
        font-size: 45px;
        color: #8c939d;
        width: 200px;
        height: 200px;
        line-height: 200px;
        text-align: center;
      }
    }
  }
}

</style>




