    <!--  -->
    <!-- element-ui -->
    <transition name="el-zoom-in-center">
      <!--  -->
      <router-view/>
    </transition>
    <!--  -->
    <!--  -->
    <footer-ly></footer-ly>
  </div>
</template>
<script>
  // 
import HeaderLy from '@/components/header'
import FooterLy from '@/components/footer'
export default {
    name: 'home',
    components: {
      HeaderLy,
      FooterLy
    },
    mounted () {
      this.$store.dispatch('getsiteoption')
    }
}
</script>



