import Vue from 'vue'
import Vuex from 'vuex'
// import
import {GetSended} from '../api/api'
import {GetProds, GetProducts, GetUsers, Sendsed, Sendsing, GetOption} from '../api/adminApi'
import {dateArray} from '../common/js/common'

Vue.use(Vuex)
// 
const state = {
  user: JSON.parse(sessionStorage.getItem('user')) || '', // 
  sended: [], // 
  adminer: JSON.parse(sessionStorage.getItem('adminer')) || '', // 
  products: [], // 
  prods: [], // 
  sendsed: [], // 
  users: [], // 
  sendsing: [], // 
  sendspay: [], // 
  siteoption: {} // 
}
// 
const mutations = {
  // 
  LOGIN (state) {
    state.user = JSON.parse(sessionStorage.getItem('user'))
  },
  // 
  LOGOUT (state) {
    state.user = ''
  },
  // 
  SENDED (state) {
    if (state.user !== '') {
      let pars = {
        sender: state.user.name,
        sendstatus: ''
      }
      GetSended(pars).then(res => {
        // console.log(res)
        state.sended = res.data.sended
      })
    }
  },
  // 
  ADMINLOGIN (state) {
    state.adminer = JSON.parse(sessionStorage.getItem('adminer'))
  },
  // 
  ADMINLOGOUT (state) {
    state.adminer = ''
  },
  // 
  PRODUCTS (state) {
    GetProducts({type: 'all'}).then(res => {
      state.products = res.data.products
    })
  },
  // 
  PRODS (state) {
    GetProds({type: 'all'}).then(res => {
      state.prods = res.data.prods
    })
  },
  // 
  SENDSED (state) {
    // 
    Sendsed().then(res => {
      state.sendsed = res.data.sendsed
    })
  },
  // 
  ALLUSERS (state) {
    GetUsers({type: 'all'}).then(res => {
      // console.log(res)
      state.users = res.data.users
    })
  },
  // 
  SENDSPAY (state) {
    Sendsing({sendstatus: ''}).then(res => {
      state.sendspay = res.data.sendsing
    })
  },
  // 
  SENDSING (state) {
    Sendsing({sendstatus: ''}).then(res => {
      state.sendsing = res.data.sendsing
    })
  },
  //  
  SITEOPTION (state) {
    GetOption().then(res => {
      state.siteoption = res.data.Option[0]
    })
  }
}
// getters
const getters = {
  // 7
  sevensend: state => {
    let sendnum = []
    for (let i = 0; i < dateArray.length; i++) {
      let nums = state.sendsed.filter(num => {
        return num.senddate === dateArray[i]
      })
      sendnum.push(nums.length)
    }
    return sendnum
  },
  // 7
  sevenuser: state => {
    let sendnum = []
    for (let i = 0; i < dateArray.length; i++) {
      let nums = state.users.filter(num => {
        return num.date === dateArray[i]
      })
      sendnum.push(nums.length)
    }
    return sendnum
  },
  // 7
  sendedlast: state => {
    let sendnum = []
    for (let i = 0; i < dateArray.length; i++) {
      let nums = state.sended.filter(num => {
        return num.senddate === dateArray[i]
      })
      sendnum.push(nums.length)
    }
    return sendnum
  },
  // 
  sender: state => {
    if (state.user !== '') {
      let sender = {
        total: state.sended.length,
        role: '',
        zhekou: '',
        num: '',
        next: ''
      }
      if (state.sended.length < 10) {
        sender.role = ''
        sender.zhekou = '10'
        sender.next = ''
        sender.num = 10 - state.sended.length
      } else if (state.sended.length < 100 && state.sended.length >= 10) {
        sender.role = ''
        sender.zhekou = '9.5'
        sender.next = ''
        sender.num = 100 - state.sended.length
      } else if (state.sended.length < 1000 && state.sended.length >= 100) {
        sender.role = ''
        sender.zhekou = '9'
        sender.next = ''
        sender.num = 1000 - state.sended.length
      } else {
        sender.role = ''
        sender.zhekou = '8.5'
        sender.next = ''
        sender.num = 10000 - state.sended.length
      }
      return sender
    } else {
      return ''
    }
  },
  // 
  sendedallleg: state => {
    let legend = []
    for (let i = 0; i < state.products.length; i++) {
      legend.push(state.products[i].name)
    }
    // console.log(legend)
    return legend
  },
  // 
  sendedall: state => {
    let sendedall = []
    for (let i = 0; i < state.products.length; i++) {
      let nums = state.sended.filter(num => {
        return num.sendtype === state.products[i].name
      })
      sendedall.push({
        value: nums.length,
        name: state.products[i].name
      })
    }
    // console.log(sendedall)
    return sendedall
  },
  // 
  sortsend: state => {
    let sendedall = []
    for (let i = 0; i < state.products.length; i++) {
      let nums = state.sendsed.filter(num => {
        return num.sendtype === state.products[i].name
      })
      sendedall.push({
        value: nums.length,
        name: state.products[i].name
      })
    }
    // console.log(sendedall)
    return sendedall
  },
  // 
  admininit: state => {
    // 
    let sellingnum = state.prods.filter(num => {
      return num.selling === true
    })
    let sendyes = state.sendsed.filter(yes => {
      return yes.senddate === dateArray[6]
    })
    let useryes = state.users.filter(yes => {
      return yes.date === dateArray[6]
    })
    let init = {
      sendedtotal: state.sendsed.length,
      sendedyes: sendyes.length,
      prepay: state.sendspay.length,
      preprint: state.sendsing.length,
      userstotal: state.users.length,
      usersyes: useryes.length,
      prodstotal: state.prods.length,
      prodsselling: sellingnum.length
    }
    return init
  }
}
// actionsmutations
const actions = {
  // 
  // LOGINlogin
  login ({commit}) {
    commit('LOGIN')
  },
  // 
  logout ({commit}) {
    commit('LOGOUT')
  },
  // 
  sended ({commit}) {
    commit('SENDED')
  },
  // 
  adminlogin ({commit}) {
    commit('ADMINLOGIN')
  },
  // 
  adminlogout ({commit}) {
    commit('ADMINLOGOUT')
  },
  // 
  products ({commit}) {
    commit('PRODUCTS')
  },
  // 
  prods ({commit}) {
    commit('PRODS')
  },
  // 
  sendsed ({commit}) {
    commit('SENDSED')
  },
  // 
  users ({commit}) {
    commit('ALLUSERS')
  },
  // 
  sendsing ({commit}) {
    commit('SENDSING')
  },
  // 
  sendspay ({commit}) {
    commit('SENDSPAY')
  },
  // 
  getsiteoption ({commit}) {
    commit('SITEOPTION')
  }
}

export default new Vuex.Store({
  state,
  mutations,
  actions,
  getters
})



