<template>
  <div class="addadminer">
    <h4></h4>
    <el-form ref="addadminer" :rules="addadminerRules" :model="addadminer">
      <el-form-item prop="name">
        <el-input v-model="addadminer.name" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="password">
        <el-input type="password" v-model="addadminer.password" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="confirmpassword">
        <el-input type="password" v-model="addadminer.confirmpassword" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="role">
        <el-select v-model="addadminer.role" placeholder="">
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
          <el-option label="" value=""></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button type="danger" round @click="newadminer"></el-button>
        <el-button type="primary" round @click="reset"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import {NewAdminer} from '../../../api/adminApi'
export default {
  // ..
  data () {
    let confirmpasswordCheck = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      } else if (value !== this.addadminer.password) {
        return callback(new Error(''))
      } else {
        return callback()
      }
    }
    return {
      addadminer: {
        name: '',
        password: '',
        confirmpassword: '',
        role: ''
      },
      addadminerRules: {
        name: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 2,
            max: 5,
            message: ' 2  5 ',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          }
        ],
        confirmpassword: [
          {
            required: true,
            validator: confirmpasswordCheck,
            trigger: 'blur'
          }
        ],
        role: [
          {
            required: true,
            message: '',
            trigger: 'change'
          }
        ]
      }
    }
  },
  methods: {
    newadminer () {
      this.$refs.addadminer.validate(valid => {
        if (valid) {
          // console.log('')
          let params = {
            name: this.addadminer.name,
            password: this.addadminer.password,
            role: this.addadminer.role
          }
          NewAdminer(params).then(res => {
            console.log(res)
            let {code, msg, name} = res.data
            if (code === 200) {
              this.$refs.addadminer.resetFields()
              this.$notify({
                title: msg,
                message: '' + name,
                type: 'success',
                offset: 100
              })
            }
          })
        } else {
          console.log('submit err')
          this.$message({
            type: 'danger',
            message: ''
          })
        }
      })
    },
    reset () {
      this.$refs.addadminer.resetFields()
    }
  }
}
</script>
<style lang="less" scoped>
@import '../../../common/less/index.less';
.addadminer {
  .learncontent;
  .el-button {
    width: 45%;
  }
  .el-select {
    width: 100%;
  }
}

</style>



