<template>
  <div class="box">
    <div class="head">
      <h3>{{this.$route.name}}</h3>
    </div>
    <div class="add">
      <h4 class="title"></h4>
      <div class="form">
        <el-form ref="sendprod" :rules="sendrules" :model="sendprod">
          <el-form-item label="" prop="sendname">
            <el-input v-model="sendprod.sendname" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sendaddr">
            <el-input v-model="sendprod.sendaddr" placeholder="()"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sendtel">
            <el-input v-model.number="sendprod.sendtel" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="recename">
            <el-input v-model="sendprod.recename" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="receaddr">
            <el-input v-model="sendprod.receaddr" placeholder="()"></el-input>
          </el-form-item>
          <el-form-item label="" prop="recetel">
            <el-input v-model.number="sendprod.recetel" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="sendprod">
            <el-select v-model="sendprod.sendprod" placeholder="">
              <template v-for="item in prods">
                <el-option :label="item.name" :value="item.name" :key="item.name"></el-option>
              </template>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model.number="sendprod.sendprice " disabled placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="sendprod.sendtype " disabled placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="sendprod.sendmsg" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="danger" @click="addsendlist"></el-button>
            <el-button type="success" @click="reset"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <!--  -->
    <div class="presend">
      <h4></h4>
      <el-table
        :data="presends"
        ref="presendsTab"
        style="width: 100%"
        @selection-change="SelectionChange"
        show-summary
        show-overflow-tooltip>
        <el-table-column
          type="selection"
          min-width="5%">
        </el-table-column>
        <el-table-column type="expand" min-width="5%">
          <template slot-scope="props">
            <el-form label-position="left" inline class="table-expand">
              <el-form-item label="">
                <span>{{ props.row.senddate }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendprod }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendprice }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendname }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendaddr }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendtel }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.recename }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.receaddr }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.recetel }}</span>
              </el-form-item>
              <el-form-item label="">
                <span>{{ props.row.sendmsg }}</span>
              </el-form-item>
            </el-form>
          </template>
        </el-table-column>
        <el-table-column
          class="tab-date"
          label=""
          min-width="20%"
          prop="senddate">
        </el-table-column>
        <el-table-column
          label=""
          min-width="25%"
          prop="recename">
        </el-table-column>
        <el-table-column
          label=""
          min-width="20%"
          prop="sendprice">
        </el-table-column>
        <el-table-column
          label=""
          min-width="25%">
          <template slot-scope="scope">
            <el-button-group>
              <el-button
                type="primary"
                size="mini"
                round
                @click="EditSend(scope.$index, scope.row)"></el-button>
              <el-button
                type="danger"
                size="mini"
                round
                @click="DelSend(scope.$index, scope.row)"></el-button>
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
      <el-button-group v-if="haspresends">
        <el-button type="primary" round @click="toggleSelection(presends)" > / </el-button>
        <el-button v-if="hasselected" type="danger" round @click="submitSend()"></el-button>
      </el-button-group>
    </div>
     <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="editsend" :rules="sendrules" :model="editsend">
        <el-form-item label="" prop="sendname">
          <el-input v-model="editsend.sendname" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sendaddr">
          <el-input v-model="editsend.sendaddr" placeholder="()"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sendtel">
          <el-input v-model.number="editsend.sendtel" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="recename">
          <el-input v-model="editsend.recename" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="receaddr">
          <el-input v-model="editsend.receaddr" placeholder="()"></el-input>
        </el-form-item>
        <el-form-item label="" prop="recetel">
          <el-input v-model.number="editsend.recetel" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="sendprod">
          <el-select v-model="editsend.sendprod" placeholder="">
            <template v-for="item in prods">
              <el-option :label="item.name" :value="item.name" :key="item.type"></el-option>
            </template>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.number="editsend.sendprice " disabled placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
            <el-input v-model="editsend.sendtype " disabled placeholder=""></el-input>
          </el-form-item>
        <el-form-item label="">
          <el-input v-model="editsend.sendmsg" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="exitEdit"> </el-button>
        <el-button type="primary" @click="submitEdit"> </el-button>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="paydialog">
      <h4>{{totalprice}}</h4>
      <p></p>
      <div>
        <img class="pay-image" src="../../../assets/er.png" alt="">
      </div>
      <!--  -->
      <el-button type="success" @click="testpay"></el-button>
    </el-dialog>
  </div>
</template>
<script>
import {GetPresends, newPreSend, DeleteSend, EditSend, UpdateSends} from '../../../api/api'
export default {
  // ..
  data () {
    let telCheck = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error(''))
      } else if (!Number.isInteger(value)) {
        return callback(new Error(''))
      } else if (value.toString().length !== 11) {
        return callback(new Error('11'))
      } else {
        callback()
      }
    }
    return {
      sendprod: {
        sendname: '',
        sendaddr: '',
        sendtel: '',
        recename: '',
        receaddr: '',
        recetel: '',
        sendprod: '',
        sendprice: '',
        sendtype: '',
        sendmsg: ''
      },
      presends: [],
      dialogFormVisible: false,
      paydialog: false,
      editsend: {
        sendname: '',
        sendaddr: '',
        sendtel: '',
        recename: '',
        receaddr: '',
        recetel: '',
        sendprod: '',
        sendprice: '',
        sendtype: '',
        sendmsg: ''
      },
      multipleSelection: [],
      sendrules: {
        sendname: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 2,
            max: 7,
            message: ' 2  7 ',
            trigger: 'blur'
          }
        ],
        sendaddr: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 12,
            max: 50,
            message: ' 12 ',
            trigger: 'blur'
          }
        ],
        sendtel: [
          {
            required: true,
            validator: telCheck,
            trigger: 'blur'
          }
        ],
        recename: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 2,
            max: 7,
            message: ' 2  7 ',
            trigger: 'blur'
          }
        ],
        receaddr: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            min: 12,
            max: 50,
            message: ' 12 ',
            trigger: 'blur'
          }
        ],
        recetel: [
          {
            required: true,
            validator: telCheck,
            trigger: 'blur'
          }
        ],
        sendprod: [
          {
            required: true,
            trigger: 'change',
            message: ''
          }
        ]
      }
    }
  },
  methods: {
    // 
    getpresends () {
      let pars = {
        sender: this.user.name,
        sendstatus: ''
      }
      GetPresends(pars).then(res => {
        this.presends = res.data.presends
      })
    },
    // 
    addsendlist () {
      this.$refs.sendprod.validate(valid => {
        if (valid) {
          let sendpar = {
            sendname: this.sendprod.sendname,
            sendaddr: this.sendprod.sendaddr,
            sendtel: this.sendprod.sendtel,
            recename: this.sendprod.recename,
            receaddr: this.sendprod.receaddr,
            recetel: this.sendprod.recetel,
            sendprod: this.sendprod.sendprod,
            sendprice: this.sendprod.sendprice,
            sendtype: this.sendprod.sendtype,
            sendmsg: this.sendprod.sendmsg,
            sender: this.user.name,
            sendstatus: ''
          }
          newPreSend(sendpar).then(res => {
            // console.log(res)
            let newsend = res.data.newsend
            if (newsend) {
              this.$notify({
                title: '',
                type: 'success',
                message: '',
                offset: 200
              })
              this.presends.unshift(newsend)
              this.$refs.sendprod.resetFields()
              this.sendprod.sendprice = ''
              this.sendprod.sendmsg = ''
              this.sendprod.sendtype = ''
            }
          })
        } else {
          this.$notify({
            title: '',
            type: 'warning',
            message: '',
            offset: 200
          })
        }
      })
    },
    // 
    reset () {
      this.$refs.sendprod.resetFields()
    },
    // 
    EditSend (index, rowIndex) {
      // console.log('edit')
      // 
      this.dialogFormVisible = true
      // 
      this.editsend = rowIndex
    },
    // 
    exitEdit () {
      this.dialogFormVisible = false
      this.$notify({
        title: '',
        type: 'warning',
        message: '',
        offset: 200
      })
    },
    // 
    submitEdit () {
      // ..
      let editpar = {
        _id: this.editsend._id,
        sendname: this.editsend.sendname,
        sendaddr: this.editsend.sendaddr,
        sendtel: this.editsend.sendtel,
        recename: this.editsend.recename,
        receaddr: this.editsend.receaddr,
        recetel: this.editsend.recetel,
        sendprod: this.editsend.sendprod,
        sendprice: this.editsend.sendprice,
        sendmsg: this.editsend.sendmsg,
        sendtype: this.editsend.sendtype,
        sender: this.user.name,
        sendstatus: ''
      }
      EditSend(editpar).then(res => {
        console.log(res)
        this.$notify({
          title: '',
          type: 'success',
          message: res.data.msg,
          offset: 200
        })
        this.dialogFormVisible = false
      })
    },
    // 
    DelSend (index, rowIndex) {
      // console.log('index', index)
      // console.log('rowIndex', rowIndex)
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        let deletePar = {
          _id: rowIndex._id
        }
        DeleteSend(deletePar).then(res => {
          // console.log(res)
          this.$notify({
            title: '',
            type: 'warning',
            message: '',
            offset: 200
          })
          this.getpresends()
        })
      }).catch(() => {
        this.$notify({
          title: '',
          type: 'info',
          message: '',
          offset: 200
        })
      })
    },
    // 
    toggleSelection (rows) {
      // console.log(rows)
      rows.forEach(row => {
        this.$refs.presendsTab.toggleRowSelection(row)
      })
    },
    // 
    SelectionChange (val) {
      this.multipleSelection = val
      console.log('', this.multipleSelection)
    },
    // 
    submitSend () {
      console.log('!')
      console.log(this.totalprice)
      this.paydialog = true
    },
    // 
    testpay () {
      let pars = []
      for (let i = 0; i < this.multipleSelection.length; i++) {
        let id = this.multipleSelection[i]._id
        let prodname = this.multipleSelection[i].sendprod
        pars.push({
          id: id,
          name: prodname
        })
      }
      console.log(pars)
      UpdateSends(pars).then(res => {
        console.log(res)
        this.$notify({
          title: '',
          type: 'success',
          message: res.data.msg,
          offset: 200
        })
        this.paydialog = false
        this.multipleSelection = []
        // 
        this.getpresends()
        // 
        this.$store.dispatch('sended')
      })
    }
  },
  computed: {
    user () {
      return this.$store.state.user
    },
    prods () {
      let allprods = this.$store.state.prods
      let cprods = allprods.filter(p => {
        return p.selling === true
      })
      return cprods
    },
    sender () {
      return this.$store.getters.sender
    },
    haspresends () {
      if (this.presends.length !== 0) {
        return true
      } else {
        return false
      }
    },
    hasselected () {
      if (this.multipleSelection.length !== 0) {
        return true
      } else {
        return false
      }
    },
    totalprice () {
      let sums = 0
      for (let i = 0; i < this.multipleSelection.length; i++) {
        sums = sums + this.multipleSelection[i].sendprice
      }
      return sums
    }
  },
  watch: {
    'sendprod.sendprod': function (val) {
      if (this.sendprod.sendprod !== '') {
        const CurProd = this.prods.filter(p => {
          return p.name === val
        })
        if (CurProd) {
          this.sendprod.sendprice = CurProd[0].price * this.sender.zhekou * 0.1
          this.sendprod.sendtype = CurProd[0].typename
        }
      } else {
        console.log('')
      }
    },
    'editsend.sendprod': function (val) {
      if (this.editsend.sendprod !== '') {
        const CurProd = this.prods.filter(p => {
          return p.name === val
        })
        if (CurProd) {
          this.editsend.sendprice = CurProd[0].price * this.sender.zhekou * 0.1
          this.editsend.sendtype = CurProd[0].typename
        }
      } else {
        console.log('')
      }
    },
    deep: true
  },
  mounted () {
    this.getpresends()
  }
}
</script>
<style lang="less" scoped>
@import '../../../common/less/index.less';
@import '../../../common/css/init.css';
.box {
  .head {
    .leftborder
  }
  .add {
    // box-shadow: 0 1px 4px @color;
    // margin: 20px 15px;
    // border-radius: 10px;
    // padding-bottom: 10px;
    .learncontent;
    .form {
      margin: 0 5px 5px 5px;
      .el-form {
        .el-select {
          width: 100%;
        }
      }
    }
  }
  .presend {
    .learncontent;
    .el-table {
      .tab-date {
        @media screen and (max-width: 768px) {
          display: none;
        }
      }
      .table-expand {
        font-size: 0;
        .el-form-item {
          color: @color;
          width: 30%;
          @media screen and (max-width: 768px) {
            width: 100%;
          }
        }
      }
    }
  }
  .el-select {
    width: 100%;
  }
  .pay-image {
    width: 120px;
    height: 120px;
  }
}
</style>




